.btnWrap{
  margin: 10vh;
  margin-top: 15vh;
  margin-bottom: 20vh;
  font-size: 2vw;
  background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(10%, rgba(255,255,255,.9)), color-stop(90%, rgba(255,255,255,.9)), to(transparent));
  background: linear-gradient(90deg, transparent 0%, rgba(255,255,255,.9) 10%, rgba(255,255,255,.9) 90%, transparent 100%); 
  width: 60vw;
  text-align: center;
  padding: 2vw;
  overflow: visible;
  display:flex;
  justify-content: space-evenly;
  align-items: center;
}

.btn {
  padding:2vw;
  text-decoration: none;
  color: black;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
}


.btn::before {
  content: ' ';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border-top-width: 3px;
  border-bottom-width: 3px;
  border-top-style: solid;
  border-bottom-style: solid;
  border-top-color: rgba(0,0,0,0.5);
  border-bottom-color: rgba(0,0,0,0.5);
  -webkit-transform: scale(0.3, 1.1);
          transform: scale(0.3, 1.1);
}

.btn:hover::before {
  opacity: 1;	
  -webkit-transform: scale(1, 1);	
          transform: scale(1, 1);	
  
}
.btn::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background-color: transparent;

}
.btn:hover::after {
  opacity: 0;	
  -webkit-transform: scale(0.1, 1);	
          transform: scale(0.1, 1);
  
}
